import React from 'react';
import PropTypes from 'prop-types';
import { Button, Form, Input } from 'antd';
import { SaveOutlined, UndoOutlined } from '@ant-design/icons';

import css from './AppPostForm.module.css';

const layout = {
  labelCol: {
    span: 5,
  },
  wrapperCol: {
    span: 17,
  },
};

/* eslint-disable no-template-curly-in-string */
const validateMessages = {
  required: ',  ${label}',
  url: ',    (: https://joeschmoe.io/api/v1/random)',
};
/* eslint-enable no-template-curly-in-string */

const AddPostForm = ({postsAmount, addPost}) => {
  const [form] = Form.useForm();
  
  const handleSubmit = ({post}) => {
    addPost(post);
    form.resetFields();
  };

  const resetDescField = () => {
    form.setFieldValue(['post', 'description'], '');
  };

  return (
    <Form 
      form={form} 
      {...layout} 
      name='nest-messages' 
      onFinish={handleSubmit} 
      validateMessages={validateMessages}
    >
      <Form.Item
        name={['post', 'title']}
        label=' '
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Input className={css.input} placeholder='  *'/>
      </Form.Item>
      
      <Form.Item 
        name={['post', 'avatar']} 
        label='   '
        rules={[
          {
            type: 'url'        
          },
        ]} 
      >
        <Input className={css.input} placeholder='   '/>
      </Form.Item>
      <Form.Item
        name={['post', 'description']} 
        label=' '
        rules={[
          {
            required: true,
            min: 3,
          },
        ]} 
      >
        <Input.TextArea placeholder=' '/>
      </Form.Item>
      <Form.Item
        wrapperCol={{
          ...layout.wrapperCol,
          offset: 5,
        }}
      >
        <Button type='text' htmlType='submit' className={css.addBtn}>
          <SaveOutlined />
          
        </Button>
        <Button 
          shape='circle'
          type='text' htmlType='button' 
          onClick={resetDescField} 
          className={css.resetTextArea}>
          <UndoOutlined />
        </Button>
      </Form.Item>
      <p className={css.postsCounter}>: {postsAmount}</p>
    </Form>
  );
};

AddPostForm.propTypes = {
  postsAmount: PropTypes.number.isRequired,
};

export default AddPostForm;